body {
  background: #fafafa;
}

p {
  font-size: 1.1em;
  font-weight: 300;
  line-height: 1.7em;
  color: #999;
}

a,
a:hover,
a:focus {
  color: #000;
  text-decoration: none;
  transition: all 0.3s;
}

.navbar {
  padding: 15px 10px;
  background: #fff;
  border: none;
  border-radius: 0;
  margin-bottom: 40px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.navbar-btn {
  box-shadow: none;
  outline: none !important;
  border: none;
}

.line {
  width: 100%;
  height: 1px;
  border-bottom: 1px dashed #ddd;
  margin: 40px 0;
}

i,
span {
  display: inline-block;

  font-family: "GothamNarrowBold", sans-serif;
  color: #181c20;
  font-size: 13px;
  font-weight: bold;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.wrapper {
  display: flex;
  align-items: stretch;
}

#sidebar {
  min-width: 280px;
  max-width: 280px;
  color: #fff;
  transition: all 0.3s;
  padding: 0px;
  display: flex;
  margin-top: 55px;
}

#sidebar.active {
  min-width: 155px;
  max-width: 155px;
  /* text-align: center; */
}

#sidebar.active .sidebar-header h3,
#sidebar.active .CTAs {
  display: none;
}

#sidebar.active .sidebar-header strong {
  display: block;
}

#sidebar ul li a {
  text-align: left;
}

#sidebar.active ul li a {
  padding: 20px 10px;
  text-align: center;
  font-size: 0.85em;
}

#sidebar.active ul li a i {
  margin-right: 0;
  display: block;
  font-size: 1.8em;
  margin-bottom: 5px;
}

#sidebar.active ul ul a {
  padding: 10px !important;
}

#sidebar.active .dropdown-toggle::after {
  top: auto;
  bottom: 10px;
  right: 50%;
  -webkit-transform: translateX(50%);
  -ms-transform: translateX(50%);
  transform: translateX(50%);
}

#sidebar .sidebar-header {
  padding: 20px;
  background: #127ed6;
}

#sidebar .sidebar-header strong {
  display: none;
  font-size: 1.8em;
}

#sidebar ul.components {
  padding: 20px 0;
  /*border-bottom: 1px solid #47748b;*/
  background: #fff;
  width: 100%;
  margin: 0;
  height: calc(100vh - 55px);
  z-index: 10;
}

#sidebar ul li a {
  padding: 10px 25px;
  font-size: 1.1em;
  display: block;
}

#sidebar ul li a i {
  margin-right: 10px;
}

a[data-toggle="collapse"] {
  position: relative;
}

.dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
  background: #6d7fcc;
}

ul.CTAs {
  padding: 20px;
}

ul.CTAs a {
  text-align: center;
  font-size: 0.9em !important;
  display: block;
  border-radius: 5px;
  margin-bottom: 5px;
}

a.download {
  background: #fff;
  color: #7386d5;
}

a.article,
a.article:hover {
  background: #6d7fcc !important;
  color: #fff !important;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
#content {
  width: 100%;
  padding: 20px;
  min-height: 100vh;
  transition: all 0.3s;
}

.sidebar-header ul li,
.components li {
  height: 62px;
  display: flex;
  align-items: center;
}

.fixed-menu {
  /* background: #0072ce !important; */
  background: radial-gradient(100% 100% at 0% 10.61%,
      #6fb0c7 13.98%,
      #035771 87.81%) !important;
  width: 155px !important;
  max-width: 155px;
  min-width: 155px !important;
}

.module-name {
  /* color: #fff;
 font-size: 0.9em !important; */
  color: #fff;
  font-size: 0.9em !important;
  font-family: "gothambook", sans-serif;
  line-height: 1.1;
  padding-top: 3px;
}

.module-name.active1 {
  color: #0e6bc5 !important;
  background: #fff !important;
}

.fixed-menu li {
  /* justify-content: center !important; */
  /* cursor: pointer; */
  height: 60px !important;
  cursor: pointer;
  margin-bottom: 0.2rem;
  /* padding-left: 0.5rem; */
}

.fixed-menu li span {
  font-size: 15px !important;
  min-width: 155px;
  padding-left: 0.5rem;
  font-weight: 300;
  padding-right: 2.5rem;
  padding-bottom: 0.3rem;
}

.active .toggle-menu {
  display: block;
}

/* .fixed-menu img{width: 24px;} */
.fixed-menu li.active {
  /* background: #0e6bc5; */
  background: #fff;
  outline: none !important;
  border-right: 3px solid #ff5c00;
}

.fixed-menu li:focus {
  outline: none !important;
}

.active1 {
  background: rgba(167, 170, 189, 0.15);
}

.toggle-menu li {
  color: #000;
  text-decoration: none;
  transition: all 0.3s;
  padding-left: 25px;
  cursor: pointer;
  font-size: 15px;
}

.toggle-menu li:focus {
  outline: none;
}

.active .toggle-menu {
  display: none;
}

.mat-icon-button {
  padding: 0;
  min-width: 0;
  width: 40px;
  height: 40px !important;
  flex-shrink: 0;
  line-height: 40px;
  border-radius: 50%;
}

.whitelogo {
  position: absolute;
  bottom: 38px;
  left: 70px;
  display: none;
}

.organe-logo {
  position: absolute;
  bottom: 38px;
  left: 97px;
  display: block;
}

.organe-logo {
  transition: all 5s;
}

nav.active .organe-logo {
  display: none;
  transition: all 5s;
}

.help_link {
  position: absolute;
  bottom: 0;
  left: 20px;
  display: flex;
  align-items: center;
  padding-right: 24px;
  z-index: 10;
  font-family: "gothambook", sans-serif;
  font-size: 15px;
  min-width: 135px;
}

.help_link span {
  font-size: 1em;
  font-weight: bold;
}

.help_active {
  position: absolute;
  bottom: 2px;
  left: 0px;
  display: flex;
  align-items: center;
  background: #0072ce;
  outline: none !important;
  padding-right: 0;
  width: 67px;
  justify-content: center;
  height: 65px;
  cursor: pointer;
}

.fixed-menu li:last-child {
  display: none;
}

.toggle-menu li:last-child {
  position: absolute;
  bottom: 3px;
  display: flex;
  align-items: center;
  padding-right: 24px;
  width: 207px;
}


@media (max-width: 1025px) {
  .whitelogo {
    display: block;
  }

  .organe-logo {
    display: none;
  }

  #sidebar.active.Mobileactive {
    left: 0;
  }
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
  .help_link {
    bottom: 18px;
  }

  .toggle-menu li:last-child {
    bottom: 0;
    padding-top: 3%;
  }

  #sidebar.active.Mobileactive {
    left: 0;
  }
}

